@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Viga&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
:root {
	--base-color: #fa4d56;
	--base-color-light: #f17077;
	--base-color-lighter: #f8c8ca;
	--base-color-dark: #f13942;
	--text-on-base-color: white;

	/* the default font size for the web is 16px - by setting 62.5%, its now set as 10px */
	font-size: 62.5%;
}

@media (max-width: 700px) {
	:root {
		font-size: 60%;
	}
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* My modifications */
* {
	box-sizing: border-box;
	outline: none;
}

a {
	text-decoration: none;
}

button {
	border: none;
	background: none;
	cursor: pointer;
}

/*
* Fonts used:
* - Viga
* - Nunito
* - Material Icons
*/

#page-home {
	width: 100vw;
	height: 100vh;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	background-color: #ffffff;
	background-attachment: fixed;
	background-size: cover;
	background-image: url(/weather-app/static/media/Pattern-Randomized.30862f82.svg);
	background-position-x: -200px;

	box-sizing: border-box;
	overflow-x: hidden;
	overflow-y: hidden;
}

@media (max-height: 800px) {
	#page-home {
		justify-content: space-between;
	}
}

header {
	display: flex;
	align-items: center;
	justify-content: center;

	position: absolute;
	top: 0;

	width: 250px;
	height: 250px;
	background-image: url(/weather-app/static/media/blob.ba6266fe.svg);
	background-repeat: no-repeat;

	color: white;
	font-family: Viga, sans-serif;
}

header h2 {
	font-size: 1.2rem;
}

header h1 {
	font-size: 3rem;
}

@media (max-height: 800px) {
	header {
		width: 100%;
		height: auto;
		margin: 30px 0 0;
		background-image: none;

		position: relative;

		color: var(--base-color);
	}
}

.search-form {
	width: 100%;
	max-width: 700px;

	margin-top: 60px;

	display: flex;
	flex-direction: column;
	align-items: center;
}

.search-form form {
	display: grid;
	grid-template-columns: 3fr 1fr;

	width: 50rem;
	height: 5rem;
	border: 2px solid var(--base-color);
	border-radius: 8px;
}

.search-form form input {
	background-color: var(--text-on-base-color);
	border: none;
	border-radius: 8px;

	color: var(--base-color);
	font-family: 'Nunito', sans-serif;
	font-weight: 700;
	font-size: 1.8rem;
	padding-left: 8px;
}

.search-form form button {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 100%;
	height: 100%;
	background-color: var(--base-color);
	transition: background-color 400ms;

	color: var(--text-on-base-color);
	font-family: 'Nunito', sans-serif;
	font-weight: 700;
	font-size: 1.8rem;
	border-top-left-radius: 0px;
	border-bottom-left-radius: 0px;
}

.search-form form button:hover {
	background-color: var(--base-color-dark);
}

.search-form form button span {
	font-family: Material Icons;
	font-size: 2.4rem;
	font-weight: 400;

	margin-right: 6px;
}

.search-form .result-container {
	width: 50rem;
	height: 30rem;

	margin-top: 5rem;

	border: 2px solid var(--base-color);
	border-radius: 8px;
	background-color: var(--text-on-base-color);

	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;

	transition: opacity 300ms;

	padding: 12px;
}

.search-form .result-container.hidden {
	opacity: 0;
}

@media (max-width: 600px) {
	.search-form form {
		width: 90%;
	}

	.search-form .result-container {
		width: 90%;
		margin-top: 4rem;
	}

	.search-form form button span {
		margin-right: 0;
	}

	.search-form form button p {
		display: none;
	}
}

.search-result {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;

	width: 100%;
	height: 100%;
}

.search-result button#btn-close {
	position: absolute;
	top: 0;
	right: 0;
	z-index: 1;

	margin-top: 10px;
	margin-right: 10px;

	background-color: var(--base-color);
	transition: background-color 400ms;
	border-radius: 50%;
	width: 25px;
	height: 25px;
	padding: 0;

	color: var(--text-on-base-color);
	font-family: Material Icons;
	font-size: 2rem;
}

.search-result button#btn-close:hover {
	background-color: var(--base-color-dark);
}

.search-result .city-name {
	color: var(--base-color);
	font-family: Nunito, sans-serif;
	font-size: 2.4rem;
	font-weight: 700;
}

.search-result .img-container {
	margin-top: 20px;

	width: 10rem;
	height: 10rem;

	background-color: var(--base-color-lighter);
	border-radius: 50%;
}

.search-result .img-container img {
	width: 10rem;
	height: auto;
}

.search-result .weather-description {
	color: var(--base-color);
	font-family: Nunito, sans-serif;
	font-size: 2rem;
	font-weight: 400;
}

.search-result .current-temperature {
	color: var(--base-color);
	font-family: Nunito, sans-serif;
	font-size: 3rem;
	font-weight: 700;

	margin-top: 30px;
}

.search-loading {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	width: 100%;
	height: 100%;
}

.search-loading img {
	width: 80px;
}

.search-loading h1 {
	color: var(--base-color);
	font-size: 2rem;
	font-family: Nunito, sans-serif;
	font-weight: 700;

	margin-top: 15px;
}

footer {
	position: absolute;
	bottom: 0;
	left: 0;

	width: 100%;

	display: flex;
	justify-content: center;
}

footer .text-container {
	width: 550px;
	height: 550px;
	background-image: url(/weather-app/static/media/footer_blob.eaea0195.svg);
	background-position-y: 420px;
	background-repeat: no-repeat;

	display: flex;
	justify-content: center;
	align-items: flex-end;
}

footer h1 {
	color: var(--text-on-base-color);
	font-family: Viga, sans-serif;
	font-size: 1.4em;
	margin-bottom: 16px;
}

footer h1 a {
	color: var(--base-color);
	background-color: white;
	border-radius: 8px;
	padding: 0 4px;
}

@media (max-height: 800px) {
	footer {
		position: relative;

		align-items: center;
		justify-content: center;
		background-color: var(--base-color);

		height: 40px;
		margin-top: 20px;
	}

	footer .text-container {
		background: none;
		width: auto;
		height: auto;
	}

	footer h1 {
		margin: 0;
	}
}

